import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastController, LoadingController, Events } from '@ionic/angular';
import { UsuariosService } from 'src/app/services/usuarios.service';
import { Nivel } from 'src/app/enums/nivel.enum';
import { Router, ActivatedRoute } from '@angular/router';
import { PacienteService } from 'src/app/services/paciente.service';
import { Paciente } from 'src/app/models/paciente';
import { NavExtrasService } from 'src/app/services/nav-extras.service';
import * as moment from 'moment'; 

@Component({
  selector: 'app-paciente-edicao',
  templateUrl: './paciente-edicao.page.html',
  styleUrls: ['./paciente-edicao.page.scss'],
})
export class PacienteEdicaoPage implements OnInit {

  form:FormGroup;
  paciente = new Paciente();
  
  constructor(public formBuilder: FormBuilder, public toastController: ToastController,
              private usuariosService:UsuariosService, private pacienteService:PacienteService,
               public router:Router, public activatedRouter:ActivatedRoute,
               public navExtra:NavExtrasService, private loadingCtrl:LoadingController,
               public events: Events) { }

  async ngOnInit() {
    this.paciente = this.navExtra.get('paciente', new Paciente());
    if (this.paciente.ultimoDiaRespondido == null) this.paciente.ultimoDiaRespondido = moment().format('YYYY-MM-DD');


    this.form = this.formBuilder.group({
      nome: [this.paciente.nome, Validators.required],
      senha: [null, (!this.paciente.uid ? [Validators.required, Validators.minLength(4)] : [Validators.minLength(4)])],
      mediaCigarros: [this.paciente.mediaCigarros, [Validators.required, Validators.min(1)]],
      precoCigarro: [this.paciente.precoCigarro, [Validators.required, Validators.min(0.1)]],
      ultimoDiaRespondido: [this.paciente.ultimoDiaRespondido, [Validators.required]],
    })
  }

  /**
   * Classe que salva (cadastra ou edita) paciente
   */
  async salvar() {
    let dados = this.form.value;
    dados.ultimoDiaRespondido =  moment(dados.ultimoDiaRespondido).format('YYYY-MM-DD');
    let load = await this.loadingCtrl.create({
      spinner:'circles',
      message: 'Aguarde'
    });
    load.present();
    if (!this.paciente.uid) { //Cadastra
      let resposta = await this.usuariosService.cadastrar(dados, Nivel.Paciente)
      if (resposta.success) {
        this.msg(`Cadastrado com sucesso. Código: ${resposta.codigo}`);
        this.router.navigateByUrl('/coordenador/pacientes')
        this.events.publish('pacientes:atualizar', true);
      } else 
        this.msg('Não foi possível salvar os dados do paciente, tente novamente mais tarde');
    } else { //Atualiza
      let resposta = await this.usuariosService.atualizar(this.paciente.uid, dados, Nivel.Paciente)
      console.log(resposta);
      if (resposta.success) {
        this.msg(`Atualizado com sucesso.`);
        this.events.publish('pacientes:atualizar', true);
      } else 
        this.msg('Não foi possível salvar os dados do paciente, tente novamente mais tarde');
    }
    load.dismiss();
  }
  /**
   * Envia uma mensagem
   * @param msg 
   */
  private msg(msg:string) {
    
    this.toastController.create({
      message: msg,
      duration: 5000
    }).then(t => t.present());
  }

}
