import { Injectable } from '@angular/core';
import * as moment from 'moment'
import * as firebase from 'firebase';
import { Paciente } from '../models/paciente';
import { Questionario } from '../models/questionario';

@Injectable({
  providedIn: 'root'
})
/**
 * Controla os dados dos questionários do paciente
 */
export class PacienteService {

    private pacienteDB: firebase.firestore.CollectionReference;
    private usuarioID:string;
    private paciente:Paciente = null;


    constructor() { 
        firebase.auth().onAuthStateChanged(user => { 
            if (user != null)
                this.usuarioID = user.uid;
        })
        this.pacienteDB = firebase.firestore().collection('pacientes');
    }

    
    //============================= BUSCAR COMO COORDENADOR =================================//
    /**
     * Busca todos os pacientes de um Coordenador
     */
    public async buscarTodos(): Promise<Paciente[]> {
        console.log(this.usuarioID);
        return this.pacienteDB.where('coordenadorUID', '==' ,this.usuarioID).where('excluido', '==', false).orderBy('nome').get().then(async snapshots => {
            let pacientes:Paciente[] = [];
            const retorno = snapshots.docs.map(async doc => {
                let paciente = Object.assign(new Paciente, doc.data());
                
                await this.pacienteDB.doc(paciente.uid).collection('questionariosDiarios').get().then(snapshot => {
                    snapshot.forEach(doc => {
                        paciente.questionariosDiarios.push(Object.assign(new Questionario, doc.data()));
                    })
                })
                return paciente;
            });
            await Promise.all(retorno).then(dados => { dados.forEach(p => {pacientes.push(Object.assign(new Paciente, p))}) })
            console.log(pacientes);
            return pacientes;
        });
    }

    /**
     * Busca o Paciente pelo seu ID unico 
     * @param uid 
     */
    public async buscar(uid:string): Promise<Paciente> {
        return this.pacienteDB
            .where('coordenadorUID', '==' ,this.usuarioID)
            .where('excluido', '==', false)
            .where('uid', '==', uid)
            .get().then(snapshot => {
                if (snapshot.empty) return null;
                return snapshot.docs[0].data() as Paciente;
            })
    }
    //============================= BUSCAR COMO PACIENTE =================================//
    /**
     * Recupera os dados do Paciente logado
     */
    public async getPacienteLogado():Promise<Paciente> {
        if (this.paciente != null && this.paciente.uid == this.usuarioID) 
            return this.paciente;

        let doc = await this.pacienteDB.doc(this.usuarioID).get();
        let dados = doc.data();
       
        await this.pacienteDB.where('excluido', '==', false)
                       .where('coordenadorUID', '==', dados.coordenadorUID)
                       .orderBy('diasSeguidosSemFumar', 'desc')
                       .get().then(snapshat => {
                            let rank = 0;
                            snapshat.forEach(doc => {
                                rank++;
                                let p = doc.data();
                                if (p.uid == this.usuarioID) dados['rank'] = rank;
                            })
                       })

        this.paciente = Object.assign(new Paciente, dados);

        await this.pacienteDB.doc(this.paciente.uid).collection('questionariosDiarios').get().then(snapshot => {
            snapshot.forEach(doc => {
                this.paciente.questionariosDiarios.push(Object.assign(new Questionario, doc.data()));
            })
        })

        return this.paciente;
    }

    /**
     * Atualiza os dados do paciente
     * @param paciente 
     */
    public atualizarPaciente(paciente:Paciente) {
        let dados = Object.assign({}, paciente);
        delete dados.questionariosDiarios;
        this.pacienteDB.doc(paciente.uid).update(dados);

        if (this.paciente.uid == paciente.uid)
            this.paciente = paciente;
    }

}